
/**
 * @author fanhualing 2018-8-24 查询账户积分|消费透支限额调整
 */
import { OnInit, Component } from "@angular/core";
import { AccountIntegralQueryBean } from "./bean/account-integral-query.bean";
import { Commfunc } from "../../../../../@uisftech/common/service/commonFun";
import { Message, ConfirmationService } from "primeng/primeng";
import { CALENDAR_CN } from "../../../../../@uisftech/common/constant/config";
import { CusViewHttpService } from "app/pages/tzb/custom/http/custom-view/custom-view.http.service";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import * as _ from 'lodash';
import { AppproResultBean, SubApproveMapBean, BussinessMap } from 'app/pages/tzb/exam-appr-comp/custom/custom-group/bean/AppproResult.bean';
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';

@Component({
    selector: 'accountIntegralQuery',
    templateUrl: './account-integral-query.component.html',
    styleUrls: ['./account-integral-query.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class AccountIntegralQueryComponent implements OnInit {

    accountQueryBean: AccountIntegralQueryBean = new AccountIntegralQueryBean();
    code: any;
    startDate: Date = null;
    endDate: Date = null;
    adjustmentTypeList: any = [];
    adjustmentModeList: any = [];
    consumeOverdrawQuotaType: any = [];
    adjustOrientation: any = [];
    msgs: Message[];
    msgs1: Message[];
    orgTreeDisplay: boolean = false;
    ownerOrgInValue: any;
    openAcctOrgName: string;
    flagOrg: string;
    handleOrgName: string;
    personPage: boolean = false;
    userInValue: any;
    agentName: string;
    tablelist: any = [];
    first: number = 0;
    totalNum: number = 0;
    ch: any = CALENDAR_CN;
    title: string;
    Display: boolean = false;
    zhanghu: boolean = true;
    upInValue: any;
    detailInvalue: any;
    statusCardAdjustment: any = [];
    unionpayDisposeResultQuery: any = [];

    user: any;
    user1: any;
    userId: string;
    userName: string;
    orgId: string;
    orgName: string;
    unionpayDisposeResult: any = [];

    // 选人
    waitSelUserList: any;
    outVal: any;
    aaaa: boolean = false;
    dealPerShow: boolean = false;

    info: any;
    valueAppSeqNo: any;
    deleteFlag: any = [];
    deleteParam: any;

    isCommitDis: boolean = true;

    // ------------fanhl------------------
    appproResultBean: AppproResultBean = new AppproResultBean();
    subApproveMapBean: SubApproveMapBean = new SubApproveMapBean();
    bussinessMap: BussinessMap = new BussinessMap();
    examineDisplay: any;
    extableData: any;
    ex: any = [];
    checkValueNode: string;  //列表选项
    checkValuePeople: string;
    checkValuePeopleSign: any = []; //股份制
    mm: any = [];
    chosseNodeIndex: string;
    taskId: string;

    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public httpService: CusOperationHttpService,
        public customeHttpService: CustomeHttpService,
    ) {
        this.codeValues();
        this.adjustmentTypeList = this.code['AdjustmentTypeList'];
        this.adjustmentModeList = this.code['AdjustmentUpdateType'];
        this.consumeOverdrawQuotaType = this.code['ConsumeOverdrawQuotaType'];
        this.adjustOrientation = this.code['AdjustOrientation'];
        this.statusCardAdjustment = this.code['statusCardAdjustment'];
        this.unionpayDisposeResult = this.code['UnionpayDisposeResult'];
        this.unionpayDisposeResultQuery = this.code['UnionpayDisposeResultQuery'];
        this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user = JSON.parse(this.commfunc.getSessionDataCH('chName'));

        // 经办人信息
        if (this.user1.userId) {
            this.userId = this.user1.userId;
        }
        if (this.user1.orgId) {
            this.orgId = this.user1.orgId;
        }
        if (this.user.userName) {
            this.userName = this.user.userName;
        }
        if (this.user.orgName) {
            this.orgName = this.user.orgName;
        }
    }

    ngOnInit() {
        this.first = 0;
        this.accountQueryBean.pageSize = 10;
        this.accountQueryBean.pageNum = 1;
        this.accountQueryBean.adjustType = '01';
        // setTimeout(() => {
        //     this.tablelist = [];
        // })
        this.querySecle();
    }

    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    // 初始化表格
    querySecle() {
        this.accountQueryBean.pageSize = 10;
        this.accountQueryBean.pageNum = 1;
        this.first = 0;
        this.doQuery();
    }

    // 查询列表
    doQuery() {
        // 校验页面数据
        if (this.accountQueryBean.adjustType == '' || this.accountQueryBean.adjustType == undefined || this.accountQueryBean.adjustType == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择调整类型！' });
            return
        }
        var regNo = /^[0-9]*$/;
        // 流水号
        if (this.accountQueryBean.appSeqNo == '') {
            this.accountQueryBean.appSeqNo = undefined;
        }
        if (this.accountQueryBean.appSeqNo != undefined) {
            if (this.accountQueryBean.appSeqNo != '') {
                if (!regNo.test(this.accountQueryBean.appSeqNo)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确流水号！' });
                    return
                }
            }
        }
        // 日期处理,如果日期为空,则不传给后台
        if (this.startDate != undefined && this.startDate != null) {
            this.accountQueryBean.startDate = this.commfunc.transDateN(this.startDate);
        } else {
            this.accountQueryBean.startDate = undefined;
        }
        if (this.endDate != undefined && this.endDate != null) {
            this.accountQueryBean.endDate = this.commfunc.transDateN(this.endDate);
        } else {
            this.accountQueryBean.endDate = undefined;
        }
        // 入参空处理
        // 调整类型
        if (this.accountQueryBean.adjustType == '' || this.accountQueryBean.adjustType == null) {
            this.accountQueryBean.adjustType = undefined;
        }
        // 调整方式
        if (this.accountQueryBean.updateType == '' || this.accountQueryBean.updateType == null) {
            this.accountQueryBean.updateType = undefined;
        }
        // 卡号
        if (this.accountQueryBean.creditCardNo == '' || this.accountQueryBean.creditCardNo == null) {
            this.accountQueryBean.creditCardNo = undefined;
        }
        //用户名称
        if (this.accountQueryBean.acctName == '' || this.accountQueryBean.acctName == null) {
            this.accountQueryBean.acctName = undefined;
        }
        //调整积分区间起
        if (this.accountQueryBean.adjustGE == '' || this.accountQueryBean.adjustGE == null) {
            this.accountQueryBean.adjustGE = undefined;
        }
        //调整积分区间止
        if (this.accountQueryBean.adjustLE == '' || this.accountQueryBean.adjustLE == null) {
            this.accountQueryBean.adjustLE = undefined;
        }
        //消费透支限额类型 
        if (this.accountQueryBean.consumeOverdrawQuotaType == '' || this.accountQueryBean.consumeOverdrawQuotaType == null) {
            this.accountQueryBean.consumeOverdrawQuotaType = undefined;
        }
        //消费方向
        if (this.accountQueryBean.adjustOrientation == '' || this.accountQueryBean.adjustOrientation == null) {
            this.accountQueryBean.adjustOrientation = undefined;
        }
        //消费限额调整区间起
        if (this.accountQueryBean.adjustGE == '' || this.accountQueryBean.adjustGE == null) {
            this.accountQueryBean.adjustGE = undefined;
        }
        //消费限额调整区间止
        if (this.accountQueryBean.adjustLE == '' || this.accountQueryBean.adjustLE == null) {
            this.accountQueryBean.adjustLE = undefined;
        }
        //审批状态
        if (this.accountQueryBean.appStatus == '' || this.accountQueryBean.appStatus == null) {
            this.accountQueryBean.appStatus = undefined;
        }
        //银联处理结果
        if (this.accountQueryBean.unionpayDisposeResult == '' || this.accountQueryBean.unionpayDisposeResult == null) {
            this.accountQueryBean.unionpayDisposeResult = undefined;
        }
        if (this.accountQueryBean.adjustType == '01') {// 账户类型
            var reg = /^[+|-]{0,1}[0-9]*$/;
            if (this.accountQueryBean.adjustGE) {
                if (!reg.test(this.accountQueryBean.adjustGE)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调整积分只能为整数！" });
                    return
                }
            }
            if (this.accountQueryBean.adjustLE) {
                if (!reg.test(this.accountQueryBean.adjustLE)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调整积分只能为整数！" });
                    return
                }
            }
            if (this.accountQueryBean.adjustLE && this.accountQueryBean.adjustLE) {
                if (parseFloat(this.accountQueryBean.adjustLE) - parseFloat(this.accountQueryBean.adjustLE) < 0) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调整起始积分不得大于调整截止积分！" });
                    return
                }
            }
        } else if (this.accountQueryBean.adjustType == '02') { //消费限额
            var reg = /^[0-9]*$/;
            if (this.accountQueryBean.adjustGE) {
                if (!reg.test(this.accountQueryBean.adjustGE)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调整后消费额度只能为数字！" });
                    return
                }
            }
            if (this.accountQueryBean.adjustLE) {
                if (!reg.test(this.accountQueryBean.adjustLE)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调整后消费额度只能为数字！" });
                    return
                }
            }
            if (this.accountQueryBean.adjustGE && this.accountQueryBean.adjustLE) {
                if (parseFloat(this.accountQueryBean.adjustLE) - parseFloat(this.accountQueryBean.adjustLE) < 0) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调整起始消费额度不得大于调整截止消费额度！" });
                    return
                }
            }
        }
        // 查询服务
        let ob = this.httpService.queryAcctIntegralOrConsumeOverdrawQuota(this.accountQueryBean);
        ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tablelist = data.infoList;
                this.totalNum = data.totalNum;
                this.isCommitDis = true;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                this.isCommitDis = true;
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
            this.isCommitDis = true;
        })
    }

    // 更改类型
    adjustTypeChange(param) {
        let id = param;
        if (param == '01') {
            this.zhanghu = true;
            this.resetDiffrence();
            this.tablelist = [];
        } else if (param == '02') {
            this.zhanghu = false;
            this.resetDiffrence();
            this.tablelist = [];
        }
        this.accountQueryBean.adjustType = id;
    }

    // 清除不同字段
    resetDiffrence() {
        this.accountQueryBean.adjustGE = undefined;
        this.accountQueryBean.adjustLE = undefined;
        this.accountQueryBean.consumeOverdrawQuotaType = undefined;
        this.accountQueryBean.adjustOrientation = undefined;
        this.accountQueryBean.adjustGE = undefined;
        this.accountQueryBean.adjustLE = undefined;
    }

    //新增
    addDisplay: boolean = false;//新增内容的开关
    add() {
        this.title = "新增";
        this.Display = true;
        this.addDisplay = true;
        this.detailsDisplay = false;
        this.updataDisplay = false;
    }

    //详情
    detailsDisplay: boolean = false;//详情内容的开关
    toDetails(col) {
        this.title = "详情";
        this.Display = true;
        this.addDisplay = false;
        this.detailsDisplay = true;
        this.updataDisplay = false;
        this.detailInvalue = col;
    }

    // 编辑
    updataDisplay: boolean = false;//编辑内容的开关
    toEdit(col) {
        this.title = "修改";
        this.Display = true;
        this.addDisplay = false;
        this.detailsDisplay = false;
        this.updataDisplay = true;
        this.upInValue = col;
    }

    // 撤销客户联系策略
    delete(item) {
        this.confirmationService.confirm({
            message: '确认要撤销吗?',
            accept: () => {
                let arr = [];
                arr.push({ 'appSeqNo': item.appSeqNo, creditCardNo: item.creditCardNo, adjustType: item.adjustType });
                let param = {
                    infoList: arr
                };
                this.httpService.deleteCeCardAppInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.querySecle()
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
                })
            }
        });
    }

    //分页
    paginate(event) {
        this.accountQueryBean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.accountQueryBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.accountQueryBean.pageSize;
        this.doQuery();
    }

    //重置
    reset() {
        this.accountQueryBean = new AccountIntegralQueryBean();
        this.openAcctOrgName = undefined;
        this.agentName = undefined;
        this.handleOrgName = undefined;
        this.startDate = null;
        this.endDate = null;
        this.accountQueryBean.adjustType = '01';
        this.accountQueryBean.pageSize = 10;
        this.accountQueryBean.pageNum = 1;
        this.first = 0;
    }

    // 开始、结束日期判断
    showTime(param1, param2) {
        if (param1 == 'plan') {
            if (this.startDate && this.endDate) {
                if (this.startDate > this.endDate) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.startDate = null;
                        } else {
                            this.endDate = null;
                        }
                    })
                }
            }
        }
        if (param1 == 'adjustGE') {
            if (this.accountQueryBean.adjustGE && this.accountQueryBean.adjustLE) {
                if (parseFloat(this.accountQueryBean.adjustGE) > parseFloat(this.accountQueryBean.adjustLE)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '开始调整区域不能大于结束调整区域' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.accountQueryBean.adjustGE = null;
                        } else {
                            this.accountQueryBean.adjustLE = null;
                        }
                    })
                }
            }
        }
    }

    // 手动输入日期
    handInputDate(param1, param2) {
        this.showTime(param1, param2);
    }

    // 修改回调
    updateCall(event) {
        this.updataDisplay = false;
        this.Display = false;
        // this.reset();
        if (event.adjustType == '01') {
            this.accountQueryBean.adjustType = '01';
            this.zhanghu = true;
        } else {
            this.accountQueryBean.adjustType = '02';
            this.zhanghu = false;
        }
        this.querySecle();
    }

    // 详情回调
    detailCall() {
        this.detailsDisplay = false;
        this.Display = false;
        this.querySecle()
    }

    // 新增回调
    addCall(event) {
        debugger
        // if (event) {

        // }
        this.addDisplay = false;
        this.Display = false;
        if (event.adjustType == '01') {// 单户新增-账户积分
            this.valueAppSeqNo = event
            this.reset();
            this.accountQueryBean.adjustType = '01';
            this.zhanghu = true;
            this.querySecle();
        } else if (event.adjustType == '02') {// 单户新增-消费限额
            this.valueAppSeqNo = event
            this.reset();
            this.accountQueryBean.adjustType = '02';
            this.zhanghu = false;
            this.querySecle();
        } else {// 批量新增-取消

        }

    }

    //选择机构
    openOrg(param) {
        this.flagOrg = param;
        this.orgTreeDisplay = !this.orgTreeDisplay;
        this.ownerOrgInValue = 'adjustOrg'
    }

    //机构树回调
    treeCall(param) {
        this.orgTreeDisplay = false;
        if (this.flagOrg == 'open') {
            this.openAcctOrgName = param.orgName;
            this.accountQueryBean.openAcctOrg = param.orgId;
        } else if (this.flagOrg == 'handle') {
            this.handleOrgName = param.orgName;
            this.accountQueryBean.handleOrg = param.orgId;
        }
    }

    //选择员工
    chooseWorker() {
        this.userInValue = '0000';
        this.personPage = !this.personPage;
    }

    //员工回调
    perInfor(item) {
        this.personPage = false;
        if (item.length > 0) {
            this.accountQueryBean.agent = item[0].tellerId;
            this.agentName = item[0].tellerName;
        }
    }

    //关闭模态框
    onHideClose() {
        this.personPage = false;
        this.orgTreeDisplay = false;
    }

    // 提交申请
    toCommit(col) {
        this.info = {};
        this.info = _.clone(col);
        if (col.appStatus == '000') {// 待发起
            this.confirmationService.confirm({
                message: '确认要提交?',
                header: '提交审核',
                accept: () => {
                    let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    if (s.extParam.specifyNextOpers) {
                        delete s.extParam.specifyNextOpers
                        let commonHeader = s;
                        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                    }
                    if (col.adjustType == '01') { // 账户积分调整-是否选人
                        this.waitSelUserList = this.commfunc.query('XYK_ZHJFTZ', {});
                    } else if (col.adjustType == '02') { // 透支余额-是否选人
                        this.waitSelUserList = this.commfunc.query('XYK_DRXFTZEDTZ', {});
                    }
                    if (!this.waitSelUserList.erro) {
                        this.outVal = this.waitSelUserList.waitSelUserList;
                        let chooseThis = this.waitSelUserList.display;
                        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                        let a2 = commonHeader.extParam.specifyNextOpers;
                        if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                            this.dealPerShow = true;
                        } else {
                            if (col.updateType == '1') {
                                this.subAudit();
                            } else if (col.updateType == '2') {
                                this.moreCommit(col);//批量
                            }
                        }
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
                    }
                }
            });
        } else if (col.appStatus == '991' || col.appStatus == '994') {// 追回、打回
            debugger
              // 根据processId查询taskId
              if (col.standby2 != null && col.standby2 != '' && col.standby2 != undefined) {
                let queryParam = {
                    processId: col.standby2,
                }
                let arr = [];
                this.customeHttpService.bpmQueryUnDoTaskList(queryParam).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        arr = data.pageData.result == null ? [] : data.pageData.result;
                        if (arr.length > 0) {
                            this.taskId = arr[0].taskId;
                            this.confirmationService.confirm({
                                message: '确认提交该批次数据？',
                                header: '提交审核',
                                accept: () => {
                                    this.subApproveMapBean.result = '0';
                                    this.bussinessMap.approve = '0';
                                    if (this.info.updateType == '1') {
                                        this.bussinessMap['dataMap'] = this.info;
                                    } else if (this.info.updateType == '2') {
                                        this.bussinessMap['dataMap'] = {
                                            adjustType: this.info.adjustType,
                                            appSeqNo: this.info.appSeqNo,
                                            batchNo: this.info.batchNo,
                                        }
                                    }
                                    setTimeout(() => {
                                        this.msgs1 = [];
                                        this.agree()
                                    }, 500)
                                },
                                reject: () => {
                                }
                            });
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: '任务不存在' });
                        }
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                })
            }
        }

    }

    outDisplayCall(event) {
        this.dealPerShow = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        if (this.info.updateType == '1') {
            this.subAudit();
        } else if (this.info.updateType == '2') {
            this.moreCommit(this.info);//批量
        }
    }

    // 单户提交
    subAudit() {
        this.isCommitDis = false;
        this.info['operationType'] = '2'; // 修改
        this.httpService.updateAcctIntegralOrConsumeOverdrawQuota(this.info).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                // this.reset();
                if (this.info.adjustType == '01') {
                    this.accountQueryBean.adjustType = '01';
                    this.zhanghu = true;
                } else {
                    this.accountQueryBean.adjustType = '02';
                    this.zhanghu = false;
                }

                if (this.info.appStatus == '000') {// 待发起
                    this.doUp();
                } else if (this.info.appStatus == '991' || this.info.appStatus == '994') {// 打回、追回
                    this.disposeSchedule();
                }
            } else {
                this.isCommitDis = true;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.isCommitDis = true;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
    }

    //取消
    onClose() {
        if (this.addDisplay && this.valueAppSeqNo && this.valueAppSeqNo['appSeqNo']) {
            let arr = [];
            arr.push({
                appSeqNo: this.valueAppSeqNo['appSeqNo'],
                adjustType: this.valueAppSeqNo['adjustType'],
            });
            let param = {
                infoList: arr
            };
            this.httpService.deleteCeCardAppInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            });
        }
    }
    //批量提交
    moreCommit(item) {
        this.isCommitDis = false;
        let param = {
            successNum: '1',
            appSeqNo: item.appSeqNo,
            batchNo: item.batchNo,
            adjustType: item.adjustType
        }
        this.httpService.submitBatchApply(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: "提交成功!" });
                // this.reset();
                if (item.adjustType == '01') {
                    this.accountQueryBean.adjustType = '01';
                    this.zhanghu = true;
                } else {
                    this.accountQueryBean.adjustType = '02';
                    this.zhanghu = false;
                }
                if (this.info.appStatus == '000') {// 待发起
                    this.doUp();
                } else if (this.info.appStatus == '991' || this.info.appStatus == '994') {// 打回、追回
                    this.disposeSchedule();
                }
                // this.doUp();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                this.isCommitDis = true;
            }
        });
    }
    //删除
    toDelete(item) {
        this.deleteFlag = [];
        this.deleteParam = JSON.parse(JSON.stringify(item));
        this.confirmationService.confirm({
            message: '确认要删除?',
            header: '删除审核',
            accept: () => {
                if (item.updateType == '2') {
                    setTimeout(() => {
                        this.deleteWho();//批量删除
                    }, 500);
                } else if (item.updateType == '1') {
                    //单户删除
                    this.deleteFlag.push({
                        appSeqNo: this.deleteParam['appSeqNo'],
                        creditCardNo: this.deleteParam['creditCardNo'],
                        adjustType: item.adjustType
                    });
                    this.commitDelete();
                }
            }
        });
    }
    //选择删除方式
    deleteWho() {
        this.confirmationService.confirm({
            message: '删除单条数据请点确认!删除该批次数据请点取消!',
            header: '删除审核',
            accept: () => {
                this.deleteFlag.push({
                    appSeqNo: this.deleteParam['appSeqNo'],
                    creditCardNo: this.deleteParam['creditCardNo'],
                    adjustType: this.deleteParam['adjustType']
                });
                this.commitDelete();
            },
            reject: () => {
                this.deleteFlag.push({
                    appSeqNo: this.deleteParam['appSeqNo'],
                    adjustType: this.deleteParam['adjustType']
                });
                this.commitDelete();
            }
        });
    }
    //确认删除
    commitDelete() {
        this.httpService.deleteCeCardAppInfo({ infoList: this.deleteFlag }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: "删除成功!" });
                this.querySecle();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        });
    }

    // 延迟查询
    doUp() {
        setTimeout(() => {
            this.querySecle();
        }, 800)
    }

    // 同意
    agree() {
        
        let param = {
            userIdMap: { userId: this.userId },
            proxessDesc: '选人策略',
            taskId: this.taskId,
            bussinessMap: this.bussinessMap,
            approveMap: this.subApproveMapBean,
        }
        this.customeHttpService.bpmFindNextNodeInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

                if (data.result.nextOutData && data.result.nextOutData.length >= 1) {
                    let waitSelectUserList = data.result.nextOutData;
                    this.extableData = [];
                    this.extableData = waitSelectUserList;
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a = commonHeader.extParam.specifyNextOpers;
                    if (a == undefined || a == '' || a == null) {
                        this.ex = [];
                        // this.juege = false;
                        if (this.extableData && this.extableData.length >= 1) {
                            if (this.extableData.length == 1 &&
                                ((this.extableData[0].selectUserList && this.extableData[0].selectUserList.length == 0) ||
                                    (this.extableData[0].selectUserList == undefined))) {
                                this.examineDisplay = false;
                                // this.juege = true;
                                this.disposeSchedule();
                            } else {
                                if (this.extableData.length == 1) {
                                    this.checkValueNode = this.extableData[0].outFlowId;
                                    if (this.extableData[0].nodeType == 'sign') {

                                    } else {
                                        this.checkValuePeopleSign = [];
                                    }
                                }
                                this.examineDisplay = true;
                            }
                        }

                    } else {
                        // this.juege = true;
                        this.disposeSchedule();
                    }
                } else {
                    this.disposeSchedule();
                }
            }
            else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        }
        )
    }

    // next
    disposeSchedule() {
        this.bussinessMap.specifyNextNode = this.checkValueNode;
        if (this.checkValuePeople) {
            let sIndex = '';
            this.extableData.forEach((element, index) => {
                if (this.checkValueNode == element.outFlowId) {
                    sIndex = index + '';
                }
            });
            if (sIndex.length > 0) {
                let num = this.checkValuePeople.length - sIndex.length;
                this.bussinessMap.specifyNextOpers = this.checkValuePeople.substring(0, num) + ',';
            } else {
                this.bussinessMap.specifyNextOpers = this.checkValuePeople + ',';
            }
        }
        this.appproResultBean.bussinessMap = this.bussinessMap;
        this.appproResultBean.userId = this.userId;
        this.appproResultBean.approveMap = this.subApproveMapBean;
        this.appproResultBean.taskId = this.taskId;
        this.customeHttpService.bpmNextTask(this.appproResultBean).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                if (this.info.adjustType == '01') {
                    this.accountQueryBean.adjustType = '01';
                    this.zhanghu = true;
                } else {
                    this.accountQueryBean.adjustType = '02';
                    this.zhanghu = false;
                }
                this.doUp();
            } else {
                this.isCommitDis = true;
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.msgs1 = [];
                this.msgs1 = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.isCommitDis = true;
            this.ex = [];
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
                delete s.extParam.specifyNextOpers
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            this.msgs1 = [];
            this.msgs1 = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    // 选择节点
    chooseNode(param, rowIndexNode) {

        if (param.outFlowId != this.checkValueNode) {
            this.checkValuePeople = undefined;
        }

        if (param.nodeType == 'sign') {
            this.mm = [];
            if (param.selectUserList && param.selectUserList.length > 0) {
                for (let i = 0; i < param.selectUserList.length; i++) {
                    this.mm.push(param.selectUserList[i].userId)
                }
            }
            this.checkValuePeopleSign = _.clone(this.mm);
        } else {
            this.checkValuePeopleSign = [];
        }
        this.chosseNodeIndex = rowIndexNode;

    }


    choosePeople(col, ri) {
        // 清空会签信息
        this.checkValuePeopleSign = [];
        // 选中当前节点
        this.checkValueNode = col.outFlowId;
    }

    // 关闭
    closeBack() {
        this.examineDisplay = false;
        this.extableData = [];
        this.checkValueNode = undefined;
        this.checkValuePeople = undefined;
        this.checkValuePeopleSign = [];
    }

    // 选人后提交
    doSubmitSure() {
        if (this.checkValueNode == '' || this.checkValueNode == undefined) {
            this.msgs1 = [];
            this.msgs1.push({ severity: "error", summary: "提示", detail: "请选择下一处理节点" })
            return
        }

        let ss = '';
        // 获取选中节点类型
        this.extableData.forEach((element, index) => {
            if (this.checkValueNode == element.outFlowId) {
                ss = element.nodeType
            }
        });

        if (this.checkValuePeople == '' || this.checkValuePeople == undefined) {
            // 如果是会签节点和自动节点
            if (ss == 'sign' || ss == 'auto' || ss == 'end') {

            } else {
                this.msgs1 = [];
                this.msgs1.push({ severity: "error", summary: "提示", detail: "请选择下一节点处理人" })
                return
            }
        }

        // this.juege = true;
        this.examineDisplay = false;

        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));

        // if (this.info.updateType == '1') {
        //     this.subAudit();
        // } else if (this.info.updateType == '2') {
        //     this.moreCommit(this.info);//批量
        // }

        this.disposeSchedule();
    }

    // 导出
    doExport() {
        let InParam = {};
        let InParamIn = {};
        InParam = _.clone(this.accountQueryBean);
        if (InParam['commonHeader']) {
            delete InParam['commonHeader']
        }
        // 日期处理,如果日期为空,则不传给后台
        if (this.startDate != undefined && this.startDate != null) {
            InParam['startDate'] = this.commfunc.transDateN(this.startDate);
        } else {
            InParam['startDate'] = undefined;
        }
        if (this.endDate != undefined && this.endDate != null) {
            InParam['endDate'] = this.commfunc.transDateN(this.endDate);
        } else {
            InParam['endDate'] = undefined;
        }
        InParamIn['condition'] = InParam;
        InParamIn['adjustType'] = '01';
        this.httpService.cecardAdjustExport(InParamIn).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let aa = data.fileName;
                window.open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(aa) + "&fileUrl=" + data.fileUrl)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }
}