import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';

import { ConfirmationService, Message } from "primeng/primeng";

import { AccountIntegralQueryUpdataBean } from "./bean/account-integral-query-updata.bean";
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { AppproResultBean, SubApproveMapBean, BussinessMap } from 'app/pages/tzb/exam-appr-comp/custom/custom-group/bean/AppproResult.bean';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import * as _ from 'lodash';

@Component({
    selector: 'accountIntegralQueryUpdata',
    templateUrl: './account-integral-query-updata.component.html',
    styleUrls: ['./account-integral-query-updata.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class AccountIntegralQueryUpdataComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    @Input('key-label') keyLabel: any;
    @Input('kind-do') kindDo: string = '';

    code: any;
    adjustmentTypeList: any = [];
    adjustmentModeList: any = [];
    consumeOverdrawQuotaType: any = [];
    adjustOrientation: any = [];
    msgs: Message[];
    msgs1: Message[];
    ch: any = CALENDAR_CN;
    zhanghu: boolean = false;
    bean: AccountIntegralQueryUpdataBean = new AccountIntegralQueryUpdataBean();
    singleAdjustmentTypeDispaly: boolean = false;//调整类型列表 必选提示 开关
    singleAdjustmentModeDispaly: boolean = false;//调整方式列表 必选提示 开关
    singleCardNoDispaly: boolean = true;//卡号输入框 单一 开关
    singleCardNoPromptDispaly: boolean = false;//卡号 必填提示 开关
    singleUserNameDispaly: boolean = true;//经办人名称 单一 开关
    singleUserNamePromptDispaly: boolean = false;//经办人名称 必填提示 开关
    singleAdjustmentIntegralDispaly: boolean = true;//当前积分 单一 开关
    singleAdjustmentIntegralPromptDispaly: boolean = false;//当前积分 必填提示 开关
    singleAdjustmentIntegralNewDispaly: boolean = true;//本次调整积分 单一 开关
    singleAdjustmentIntegralNewPromptDispaly: boolean = false;//本次调整积分 单一 必填提示 开关
    threeRowDispaly: boolean = true;//开启第三行输入框
    fourRowDispaly: boolean = true;//开启第四行输入框
    singleOpeningAgencyPromptDispaly: boolean = false;//开户机构 单一 必选提示 开关
    singleManagerPromptDispaly: boolean = false;//经办人 单一 必填提示 开关
    singleManagerAgencyPromptDispaly: boolean = false;//经办机构 单一 必填提示 开关
    singleManagerDatePromptDispaly: boolean = false;//经办日期 单一 必填提示 开关


    // 选人
    waitSelUserList: any;
    outVal: any;
    aaaa: boolean = false;
    // 待办
    // ------------fanhl------------------
    appproResultBean: AppproResultBean = new AppproResultBean();
    subApproveMapBean: SubApproveMapBean = new SubApproveMapBean();
    bussinessMap: BussinessMap = new BussinessMap();
    examineDisplay: any;
    extableData: any;
    ex: any = [];
    user: any;
    userId: string;//用户id
    checkValueNode: string;  //列表选项
    checkValuePeople: string;
    checkValuePeopleSign: any = []; //股份制
    mm: any = [];
    chosseNodeIndex: string;
    taskId: string;

    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public httpService: CusOperationHttpService,
        public customeHttpService: CustomeHttpService,
    ) {
        //调用码值
        this.codeValues();
        this.adjustmentTypeList = this.code['AdjustmentTypeList'];
        this.adjustmentModeList = this.code['AdjustmentUpdateType'];
        this.consumeOverdrawQuotaType = this.code['ConsumeOverdrawQuotaType'];
        this.adjustOrientation = this.code['AdjustOrientation'];

        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
    }

    // 获取码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    ngOnInit() {
        debugger
        if (this.inValue) {
            if (this.inValue.adjustType == '01') {
                this.zhanghu = true;
            } else {
                this.zhanghu = false;
            }
            for (let i in this.inValue) {
                this.bean[i] = this.inValue[i];
            }
        }

    }

    doSubmitPre(type) {

        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        if (this.bean.creditCardNo == undefined || this.bean.creditCardNo == null || this.bean.creditCardNo == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "卡号不能为空！" });
            return
        }
        var regCreditCardNo = /^[0-9]*$/;
        if (!regCreditCardNo.test(this.bean.creditCardNo)) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "卡号只能为数字！" });
            return
        }

        if (this.bean.creditCardNo.length < 15 || this.bean.creditCardNo.length > 19) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "卡号长度为15位至19位！" });
            return
        }
        // 增加*号校验
        if (this.bean.acctName == undefined || this.bean.acctName == null || this.bean.acctName == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "账户名称不能为空！" });
            return
        }
        if (this.bean.openAcctOrgName == undefined || this.bean.openAcctOrgName == null || this.bean.openAcctOrgName == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "开户机构不能为空！" });
            return
        }
        if (this.zhanghu) {
            var reg = /^[+|-]{0,1}[0-9]*$/;
            if (this.inValue.adjustType == '01') {
                if (this.bean.integralAdjustAfter != undefined && this.bean.integralAdjustAfter != '') {
                    if (!reg.test(this.bean.integralAdjustAfter)) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: "调整积分只能为整数！" });
                        return
                    }
                    if (parseFloat(this.bean.integralCurrent) + parseFloat(this.bean.integralAdjustAfter) < 0) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: "调整积分不足！" });
                        return
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调整积分不能为空！" });
                    return
                }
            }
        }
        if (!this.zhanghu) {
            // bean.integralAdjustAfter
            var reg = /^[0-9]*$/;
            if (this.bean.adjustOrientation == undefined || this.bean.adjustOrientation == '') {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调整方向不能为空！" });
                return
            }

            if (this.bean.consumeAmtAdjustAfter == undefined || this.bean.consumeAmtAdjustAfter == '') {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调整后消费额度不能为空！" });
                return
            }
            if (!reg.test(this.bean.consumeAmtAdjustAfter)) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调整后消费额度只能为数字！" });
                return
            }
            if (this.inValue.adjustType == '02') {
                if (this.bean.adjustOrientation == '1') {
                    if (parseFloat(this.bean.consumeAmtAdjustAfter) < parseFloat(this.bean.consumeAmtCurrent) ||
                        parseFloat(this.bean.consumeAmtAdjustAfter) == parseFloat(this.bean.consumeAmtCurrent)) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: "调整后消费额度不得低于当前消费额度！" });
                        return
                    }
                }
                if (this.bean.adjustOrientation == '2') {
                    if (parseFloat(this.bean.consumeAmtAdjustAfter) > parseFloat(this.bean.consumeAmtCurrent) ||
                        parseFloat(this.bean.consumeAmtAdjustAfter) == parseFloat(this.bean.consumeAmtCurrent)) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: "调整后消费额度不得高于当前消费额度！" });
                        return
                    }
                }
            }
        }
        if (type == 'save') {
            // 修改
            this.httpService.updateCeCardAdjustApp(this.bean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                    this.doUp();
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            }, error => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
            })
        } else if (type == 'commit') {
            if (this.inValue.appStatus == '000') {// 待发起
                // 账户积分调整-是否选人
                if (this.bean.adjustType == '01') {
                    this.waitSelUserList = this.commfunc.query('XYK_ZHJFTZ', {});
                } else if (this.bean.adjustType == '02') {
                    this.waitSelUserList = this.commfunc.query('XYK_DRXFTZEDTZ', {});
                }
                if (!this.waitSelUserList.erro) {
                    this.outVal = this.waitSelUserList.waitSelUserList;
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a = commonHeader.extParam.specifyNextOpers;
                    if (this.waitSelUserList.display && (a == undefined || a == '' || a == null)) {
                        this.aaaa = true;
                    } else {
                        this.doSubmit();
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
                }
            } else if (this.inValue.appStatus == '991' || this.inValue.appStatus == '994') {// 追回、打回
                // 根据processId查询taskId
                if (this.inValue.standby2 != null && this.inValue.standby2 != '' && this.inValue.standby2 != undefined) {
                    let queryParam = {
                        processId: this.inValue.standby2,
                    }
                    let arr = [];
                    this.customeHttpService.bpmQueryUnDoTaskList(queryParam).subscribe(data => {
                        if (data.returnCode.code == "success") {
                            arr = data.pageData.result == null ? [] : data.pageData.result;
                            if (arr.length > 0) {
                                this.taskId = arr[0].taskId;
                                this.confirmationService.confirm({
                                    message: '确认提交该批次数据？',
                                    header: '提交审核',
                                    accept: () => {
                                        this.upAccountIntegral();
                                    },
                                    reject: () => {
                                    }
                                });
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '提示', detail: '任务不存在' });
                            }
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                        }
                    }, error => {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                    })
                }
            }

        }
    }

    // 提交
    doSubmit() {
        this.bean.operationType = '2' // 修改标识
        this.httpService.updateAcctIntegralOrConsumeOverdrawQuota(this.bean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                this.doUp();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
    }

    outDisplayCall(event) {
        this.aaaa = false;
    }

    // 选人之后回调
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.doSubmit();

    }

    // 关闭模态框
    doUp() {
        setTimeout(() => {
            this.outValue.emit({ adjustType: this.bean.adjustType });
        }, 500)
    }

    // 修改账户积分
    upAccountIntegral() {
        let param = _.clone(this.bean);
        this.httpService.updateCeCardAdjustApp(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (this.inValue.updateType == '1') {
                    this.bussinessMap['dataMap'] = this.bean;
                } else if (this.inValue.updateType == '2') {
                    this.bussinessMap['dataMap'] = {
                        adjustType: this.inValue.adjustType,
                        appSeqNo: this.inValue.appSeqNo,
                        batchNo: this.inValue.batchNo,
                    }
                }
                setTimeout(() => {
                    this.msgs1 = [];
                    this.agree()
                }, 500)
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    // 同意
    agree() {
        // 同意
        this.subApproveMapBean.result = '0';
        this.bussinessMap.approve = '0';

        let param = {
            userIdMap: { userId: this.userId },
            proxessDesc: '选人策略',
            taskId: this.taskId,
            bussinessMap: this.bussinessMap,
            approveMap: this.subApproveMapBean,
        }
        this.customeHttpService.bpmFindNextNodeInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

                if (data.result.nextOutData && data.result.nextOutData.length >= 1) {
                    let waitSelectUserList = data.result.nextOutData;
                    this.extableData = [];
                    this.extableData = waitSelectUserList;
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a = commonHeader.extParam.specifyNextOpers;
                    if (a == undefined || a == '' || a == null) {
                        this.ex = [];
                        // this.juege = false;
                        if (this.extableData && this.extableData.length >= 1) {
                            if (this.extableData.length == 1 &&
                                ((this.extableData[0].selectUserList && this.extableData[0].selectUserList.length == 0) ||
                                    (this.extableData[0].selectUserList == undefined))) {
                                this.examineDisplay = false;
                                // this.juege = true;
                                this.disposeSchedule();
                            } else {
                                if (this.extableData.length == 1) {
                                    this.checkValueNode = this.extableData[0].outFlowId;
                                    if (this.extableData[0].nodeType == 'sign') {

                                    } else {
                                        this.checkValuePeopleSign = [];
                                    }
                                }
                                this.examineDisplay = true;
                            }
                        }

                    } else {
                        // this.juege = true;
                        this.disposeSchedule();
                    }
                } else {
                    this.disposeSchedule();
                }
            }
            else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        }
        )
    }

    // next
    disposeSchedule() {
        this.bussinessMap.specifyNextNode = this.checkValueNode;
        if (this.checkValuePeople) {
            let sIndex = '';
            this.extableData.forEach((element, index) => {
                if (this.checkValueNode == element.outFlowId) {
                    sIndex = index + '';
                }
            });
            if (sIndex.length > 0) {
                let num = this.checkValuePeople.length - sIndex.length;
                this.bussinessMap.specifyNextOpers = this.checkValuePeople.substring(0, num) + ',';
            } else {
                this.bussinessMap.specifyNextOpers = this.checkValuePeople + ',';
            }
        }
        this.appproResultBean.bussinessMap = this.bussinessMap;
        this.appproResultBean.userId = this.userId;
        this.appproResultBean.approveMap = this.subApproveMapBean;
        this.appproResultBean.taskId = this.taskId;
        this.customeHttpService.bpmNextTask(this.appproResultBean).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.doUp();
            } else {
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.msgs1 = [];
                this.msgs1 = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.ex = [];
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
                delete s.extParam.specifyNextOpers
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            this.msgs1 = [];
            this.msgs1 = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    // 选择节点
    chooseNode(param, rowIndexNode) {

        if (param.outFlowId != this.checkValueNode) {
            this.checkValuePeople = undefined;
        }

        if (param.nodeType == 'sign') {
            this.mm = [];
            if (param.selectUserList && param.selectUserList.length > 0) {
                for (let i = 0; i < param.selectUserList.length; i++) {
                    this.mm.push(param.selectUserList[i].userId)
                }
            }
            this.checkValuePeopleSign = _.clone(this.mm);
        } else {
            this.checkValuePeopleSign = [];
        }
        this.chosseNodeIndex = rowIndexNode;

    }


    choosePeople(col, ri) {
        // 清空会签信息
        this.checkValuePeopleSign = [];
        // 选中当前节点
        this.checkValueNode = col.outFlowId;
    }

    // 关闭
    closeBack() {
        this.examineDisplay = false;
        this.extableData = [];
        this.checkValueNode = undefined;
        this.checkValuePeople = undefined;
        this.checkValuePeopleSign = [];
    }

    // 选人后提交
    doSubmitSure() {
        if (this.checkValueNode == '' || this.checkValueNode == undefined) {
            this.msgs1 = [];
            this.msgs1.push({ severity: "error", summary: "提示", detail: "请选择下一处理节点" })
            return
        }

        let ss = '';
        // 获取选中节点类型
        this.extableData.forEach((element, index) => {
            if (this.checkValueNode == element.outFlowId) {
                ss = element.nodeType
            }
        });

        if (this.checkValuePeople == '' || this.checkValuePeople == undefined) {
            // 如果是会签节点和自动节点
            if (ss == 'sign' || ss == 'auto' || ss == 'end') {

            } else {
                this.msgs1 = [];
                this.msgs1.push({ severity: "error", summary: "提示", detail: "请选择下一节点处理人" })
                return
            }
        }

        // this.juege = true;
        this.examineDisplay = false;
        this.disposeSchedule()

    }

}