import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ConfirmationService, Message } from "primeng/primeng";
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { AccountIntegralUpdataExamBean } from 'app/pages/tzb/element-adjustment/account-integral/account-integral-query-updata-exam/bean/account-integral-updata-exam.bean';

@Component({
    selector: 'accountIntegralUpdataExam',
    templateUrl: './account-integral-updata-exam.component.html',
    styleUrls: ['./account-integral-updata-exam.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class AccountIntegralUpdataExamComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    @Input('key-label') keyLabel: any;
    @Input('kind-do') kindDo: string = '';

    code: any;
    adjustmentTypeList: any = [];
    adjustmentModeList: any = [];
    consumeOverdrawQuotaType: any = [];
    adjustOrientation: any = [];
    msgs: Message[];
    ch: any = CALENDAR_CN;
    zhanghu: boolean = false;
    bean: AccountIntegralUpdataExamBean = new AccountIntegralUpdataExamBean();
    singleAdjustmentTypeDispaly: boolean = false;//调整类型列表 必选提示 开关
    singleAdjustmentModeDispaly: boolean = false;//调整方式列表 必选提示 开关
    singleCardNoDispaly: boolean = true;//卡号输入框 单一 开关
    singleCardNoPromptDispaly: boolean = false;//卡号 必填提示 开关
    singleUserNameDispaly: boolean = true;//经办人名称 单一 开关
    singleUserNamePromptDispaly: boolean = false;//经办人名称 必填提示 开关
    singleAdjustmentIntegralDispaly: boolean = true;//当前积分 单一 开关
    singleAdjustmentIntegralPromptDispaly: boolean = false;//当前积分 必填提示 开关
    singleAdjustmentIntegralNewDispaly: boolean = true;//本次调整积分 单一 开关
    singleAdjustmentIntegralNewPromptDispaly: boolean = false;//本次调整积分 单一 必填提示 开关
    threeRowDispaly: boolean = true;//开启第三行输入框
    fourRowDispaly: boolean = true;//开启第四行输入框
    singleOpeningAgencyPromptDispaly: boolean = false;//开户机构 单一 必选提示 开关
    singleManagerPromptDispaly: boolean = false;//经办人 单一 必填提示 开关
    singleManagerAgencyPromptDispaly: boolean = false;//经办机构 单一 必填提示 开关
    singleManagerDatePromptDispaly: boolean = false;//经办日期 单一 必填提示 开关


    // 选人
    waitSelUserList: any;
    outVal: any;
    aaaa: boolean = false;


    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public httpService: CusOperationHttpService,
    ) {
        //调用码值
        this.codeValues();
        this.adjustmentTypeList = this.code['AdjustmentTypeList'];
        this.adjustmentModeList = this.code['AdjustmentUpdateType'];
        this.consumeOverdrawQuotaType = this.code['ConsumeOverdrawQuotaType'];
        this.adjustOrientation = this.code['AdjustOrientation'];
    }

    // 获取码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    ngOnInit() {
        if (this.inValue) {
            if (this.inValue.adjustType == '01') {
                this.zhanghu = true;
            } else {
                this.zhanghu = false;
            }
            for (let i in this.inValue) {
                this.bean[i] = this.inValue[i];
            }
        }

    }

    doSubmitPre(type) {

        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        if (this.bean.creditCardNo == undefined || this.bean.creditCardNo == null || this.bean.creditCardNo == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "卡号不能为空！" });
            return
        }
        var regCreditCardNo = /^[0-9]*$/;
        if (!regCreditCardNo.test(this.bean.creditCardNo)) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "卡号只能为数字！" });
            return
        }

        if (this.bean.creditCardNo.length < 15 || this.bean.creditCardNo.length > 19) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "卡号长度为15位至19位！" });
            return
        }
        // 增加*号校验
        if (this.bean.acctName == undefined || this.bean.acctName == null || this.bean.acctName == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "账户名称不能为空！" });
            return
        }
        if (this.bean.openAcctOrgName == undefined || this.bean.openAcctOrgName == null || this.bean.openAcctOrgName == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "开户机构不能为空！" });
            return
        }

        if (this.zhanghu) {
            var reg = /^[+|-]{0,1}[0-9]*$/;
            if (this.inValue.adjustType == '01') {
                if (this.bean.integralAdjustAfter != undefined && this.bean.integralAdjustAfter != '') {
                    if (!reg.test(this.bean.integralAdjustAfter)) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: "调整积分只能为整数！" });
                        return
                    }
                    if (parseFloat(this.bean.integralCurrent) + parseFloat(this.bean.integralAdjustAfter) < 0) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: "调整积分不足！" });
                        return
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调整积分不能为空！" });
                    return
                }
            }
        }
        if (!this.zhanghu) {
            // bean.integralAdjustAfter
            var reg = /^[0-9]*$/;
            if (this.bean.adjustOrientation == undefined || this.bean.adjustOrientation == '') {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调整方向不能为空！" });
                return
            }

            if (this.bean.consumeAmtAdjustAfter == undefined || this.bean.consumeAmtAdjustAfter == '') {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调整后消费额度不能为空！" });
                return
            }
            if (!reg.test(this.bean.consumeAmtAdjustAfter)) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调整后消费额度只能为数字！" });
                return
            }
            if (this.inValue.adjustType == '02') {
                if (this.bean.adjustOrientation == '1') {
                    if (parseFloat(this.bean.consumeAmtAdjustAfter) < parseFloat(this.bean.consumeAmtCurrent) ||
                        parseFloat(this.bean.consumeAmtAdjustAfter) == parseFloat(this.bean.consumeAmtCurrent)) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: "调整后消费额度不得低于当前消费额度！" });
                        return
                    }
                }
                if (this.bean.adjustOrientation == '2') {
                    if (parseFloat(this.bean.consumeAmtAdjustAfter) > parseFloat(this.bean.consumeAmtCurrent) ||
                        parseFloat(this.bean.consumeAmtAdjustAfter) == parseFloat(this.bean.consumeAmtCurrent)) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: "调整后消费额度不得高于当前消费额度！" });
                        return
                    }
                }
            }
            if (this.bean.adjustReason == undefined || this.bean.adjustReason == '') {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调整原因不能为空！" });
                return
            }
            if (this.bean.adjustReason.length > 200) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调整原因最多可输入200个字符" });
                return
            }

        }
        if (type == 'save') {
            // 修改
            this.httpService.updateCeCardAdjustApp(this.bean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                    this.doUp();
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            }, error => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
            })
        } else if (type == 'commit') {
            // 账户积分调整-是否选人
            if (this.bean.adjustType == '01') {
                this.waitSelUserList = this.commfunc.query('XYK_ZHJFTZ', {});
            } else if (this.bean.adjustType == '02') {
                this.waitSelUserList = this.commfunc.query('XYK_DRXFTZEDTZ', {});
            }
            if (!this.waitSelUserList.erro) {
                this.outVal = this.waitSelUserList.waitSelUserList;
                let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                let a = commonHeader.extParam.specifyNextOpers;
                if (this.waitSelUserList.display && (a == undefined || a == '' || a == null)) {
                    this.aaaa = true;
                } else {
                    this.doSubmit();
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
            }
        }
    }

    // 提交
    doSubmit() {
        this.bean.operationType = '2' // 修改标识
        this.httpService.updateAcctIntegralOrConsumeOverdrawQuota(this.bean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
                this.doUp();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
    }

    outDisplayCall(event) {
        this.aaaa = false;
    }

    // 选人之后回调
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.doSubmit();

    }

    // 关闭模态框
    doUp() {
        setTimeout(() => {
            this.outValue.emit({adjustType: this.bean.adjustType});
        }, 500)
    }

}