import { Component, OnInit } from "@angular/core";
import { Commfunc } from "../../../../../@uisftech/common/service/commonFun";
import { ConfirmationService, Message } from "primeng/primeng";
import { CALENDAR_CN } from "../../../../../@uisftech/common/constant/config";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { ActivatedRoute } from '@angular/router';
import { CardElementsAdjustmentBean } from "app/pages/tzb/element-adjustment/account-integral/account-integral-exam/bean/account-integral-exam.bean";
import * as _ from 'lodash';
import { tableMessage } from 'app/@uisftech/common/constant/config';

@Component({
    selector: 'accountIntegralExam',
    templateUrl: './account-integral-exam.component.html',
    styleUrls: ['./account-integral-exam.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class AccountIntegralExamComponent implements OnInit {

    user: any;
    user1: any;
    userId: string;
    userName: string;
    orgId: string;
    orgName: string;
    handleDate: string;
    code: any;
    bean: CardElementsAdjustmentBean = new CardElementsAdjustmentBean();
    msgs: Message[];
    ch: any = CALENDAR_CN;
    nameList: any = [];
    noPromptDispaly: boolean = false;
    detailList: any = [];
    adjustmentTypeList: any = [];
    adjustmentModeList: any = [];
    consumeOverdrawQuotaType: any = [];
    adjustOrientation: any = [];
    tablelist: any = [];

    zhanghudanhu: boolean = false;
    xianedanhu: boolean = false;
    zhanghupiliang: boolean = false;
    xianepiliang: boolean = false;
    first: number = 0;
    totalNum: number = 0;

    taskRate: string;
    canUp: boolean = false;
    flag: string;
    isHistory: boolean = false;
    integralInfo: any;

    title: string;
    updataDisplay: boolean = false; 
    Display: boolean = false; 
    upInValue: any;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    beanExam: any;

    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public httpService: CusOperationHttpService,
        public routeInfo: ActivatedRoute,
    ) {
        debugger
        //调用码值
        this.codeValues();
        this.adjustmentTypeList = this.code['AdjustmentTypeList'];
        this.adjustmentModeList = this.code['AdjustmentUpdateType'];
        this.consumeOverdrawQuotaType = this.code['ConsumeOverdrawQuotaType'];
        this.adjustOrientation = this.code['AdjustOrientation'];

        // 获取参数
        this.detailList = this.routeInfo.snapshot.params;
        this.beanExam = JSON.parse(this.routeInfo.snapshot.params.dataMap)
        // this.bean = JSON.parse(this.routeInfo.snapshot.params.dataMap)
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;

        this.taskRate = this.routeInfo.snapshot.params['taskRate'];
        this.flag = this.routeInfo.snapshot.params['markFlag'];
        if (this.flag == '1') {
            this.isHistory = false;
        } else {
            this.isHistory = true;
        }
        if (this.taskRate == '1' && !this.isHistory) {
            this.canUp = true;
        } else {
            this.canUp = false;
        }
    }


    // 获取码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    ngOnInit() {

        debugger
        this.bean.updateType = this.beanExam.updateType;
        this.bean.adjustType = this.beanExam.adjustType;
        this.bean.appSeqNo = this.beanExam.appSeqNo;
        this.bean.creditCardNo = this.beanExam.creditCardNo;
        this.bean.batchNo = this.beanExam.batchNo;

        if (this.bean.updateType) {
            if (this.bean.adjustType == '01' && this.bean.updateType == '1') {
                // 信用卡账户单户
                this.zhanghudanhu = true;
            } else if (this.bean.adjustType == '02' && this.bean.updateType == '1') {
                // 消费透支限额单户
                this.xianedanhu = true;
            } else if (this.bean.adjustType == '01' && this.bean.updateType == '2') {
                // 信用卡账户单户
                this.zhanghudanhu = true;
            } else if (this.bean.adjustType == '02' && this.bean.updateType == '2') {
                // 消费透支限额单户
                this.xianedanhu = true;
            }
            this.queryExamInfo();
        } else {
            // 信用卡账户批量
            if (this.bean.adjustType == '01') {
                this.zhanghupiliang = true;
            } else if (this.bean.adjustType == '02') {
                this.xianepiliang = true
            }

            let param = {
                appSeqNo: this.bean.appSeqNo,  // 流水号
                batchNo: this.bean.batchNo, // 批次号
            }
            this.queryExamInfo()
        }

    }

    // 查询信用卡信息
    queryExamInfo(){
        
        let param1 = {};
        if(this.zhanghudanhu || this.xianedanhu){
            param1 = {
                appSeqNo: this.bean.appSeqNo,
                creditCardNo: this.bean.creditCardNo,
            }
        }
        if (this.zhanghupiliang || this.xianepiliang) {
            param1 = {
                appSeqNo: this.bean.appSeqNo,
                pageSize: this.bean.pageSize,
                pageNum: this.bean.pageNum,
            }
        }
        
        let ob = this.httpService.queryCeCardAdjustApp(param1);
        ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tablelist = data.infoList == null ? [] : data.infoList;
                this.totalNum = data.totalNum;
                if(this.tablelist.length > 0){
                    let dataItem = this.tablelist[0];

                    for (let item in dataItem) {
                        this.bean[item] = dataItem[item];
                    }
                }
               
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }

    // 初始化表格
    querySecle() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;
        this.queryExamInfo();
    }

    // 查询列表
    doQuery() {
        let ob = this.httpService.queryAcctIntegralOrConsumeOverdrawQuota(this.bean);
        ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tablelist = data.infoList;
                this.totalNum = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })

    }

    //分页
    paginate(event) {
        this.bean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.queryExamInfo();
    }

    // 改变值
    changeValue() {
        this.integralInfo = _.clone(this.bean)
    }

    // 批量-编辑
    toEdit(col) {
        this.title = '修改';
        this.updataDisplay = true; 
        this.Display = true;
        this.upInValue = col;
    }

    // 批量-删除
    delete(col) {
        if (this.totalNum < 2) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '至少保留一条数据' }];
            return
        }

        let arr = [];
        arr.push(
            // 流水号 + 卡号 + 调整类型
            { appSeqNo: col.appSeqNo, creditCardNo: col.creditCardNo, adjustType: col.adjustType }
        )
        let param = {
            infoList: arr
        }
        this.httpService.deleteCeCardAppInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // 重新查询
                this.querySecle();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    // 打回拟稿人修改
    updateCall() {
        this.updataDisplay = false;
        this.Display = false;
        this.querySecle();
    }

}